import React from 'react';
import styles from './Skills.module.scss';
import Skill from "./skill/Skill";
import jsImage from '../img/js-file.svg'
import reactImage from '../img/react.svg'
import cssImage from '../img/css.png'
import tsImage from '../img/TS.png'
import reduxImage from '../img/redux.png'
import htmlImage from '../img/html.svg'
import Title from "../common/components/title/Title";


function Skills() {


    return (
        <div id='skills' className={styles.skillsBlock}>
            <div className={styles.container}>
                <Title text={'Skills'}/>
                <div className={styles.skills}>
                    <Skill title={'JS'}
                           skillIcon={jsImage}/>
                    <Skill title={'HTML'}
                           skillIcon={htmlImage}/>
                    <Skill title={'React'}
                           skillIcon={reactImage}/>
                    <Skill title={'TS'}
                           skillIcon={tsImage}/>
                    <Skill title={'CSS'}
                           skillIcon={cssImage}/>
                    <Skill title={'Redux'}
                           skillIcon={reduxImage}/>
                </div>
            </div>
        </div>
    );
}


export default Skills;